import React from "react"
import addToMailchimp from 'gatsby-plugin-mailchimp';

import Layout from "../components/layout"
import SEO from "../components/seo"
import MaxPronkoImage from "../components/max-pronko"
import 'bootstrap/dist/css/bootstrap.css';
import {Link} from "gatsby";

export default class IndexPage extends React.Component {
  state = {
    email: '',
    showMessage: false,
    messageClassName: ''
  }

  _handleChange = (e) => {
    this.setState({
      [`${e.target.name}`]: e.target.value,
    });
  }

  _handleSubmit = (e) => {
    e.preventDefault();

    console.log('submit', this.state);
    var self = this;
    addToMailchimp(this.state.email, this.state)
        .then(({ msg, result }) => {
          if (result !== 'success') {
            throw msg;
          }

          self.setState({
            email: '',
            showMessage: true,
            message: msg,
            messageClassName: 'text-center alert alert-success'
          });
        })
        .catch((err) => {
          self.setState({
            showMessage: true,
            message: err,
            messageClassName: 'text-center alert alert-warning'
          });
        });
  }
  render() {
    return (
      <Layout>
        <SEO title="The ultimate learning resource for Magento 2 Developers" />
          <div className="index-page">
            <div className="main-banner jumbotron jumbotron-fluid mb-5 ">
              <div className="container text-center pt-5 pb-5">
                <h1 className="display-4 mb-4">Master Your Magento 2</h1>
                <p className="lead mb-5">Ultimate learning Magento 2 resource for PHP and JavaScript developers</p>
                <Link className="btn btn-primary mt-5" to="/courses" role="button">Explore Courses</Link>
              </div>
            </div>
            <div className="container mb-5 pt-5 pb-5">
              <h2 className="text-center mb-4">From Basic to Advanced</h2>
              <p className="lead text-center">Learn to code with our step-by-step video tutorials that take you from a junior to a professional Magento Developer. Real World code examples will give you the required skills for your next career move</p>
            </div>
            <div className="container">
              <div className="jumbotron choose-path">
                  <h2 className="text-center mb-5">Choose Your Path</h2>
                  <p className="lead text-center">The easy-to-navigate and follow video tutorials will drive you from the very basics to an advanced level of skills and knowledge of Magento 2</p>
                  <div className="container mt-5">
                    <div className="row">
                      <div className="col">
                        <h3>Back End Developer</h3>
                        <p>Gain a deep understanding of Magento 2 development concepts and experience in implementing and customizing Magento 2</p>
                      </div>
                      <div className="col">
                        <h3>Front End Developer</h3>
                        <p>Upgrade your skills and knowledge of the Magento 2 JavaScript framework and its application and become a certified professional developer</p>
                      </div>
                    </div>
                  </div>
              </div>
            </div>
            <div className="container text-center mt-5 mb-5 pt-5 pb-5">
              <h2 className="mb-4">Easy to Follow, Fast to Learn</h2>
              <p className="lead">Whether you want to become a frontend developer and build Magento 2 themes or a PHP backend developer, who brings integrations into e-commerce world</p>
            </div>
              <div className="container text-center mt-5 mb-5 pt-5 pb-5">
                  <h2 className="mb-4">Meet Your Teacher</h2>
                  <div className="row">
                      <div className="col col-lg-4" />
                      <div className="col-md-auto">
                          <div className="mx-auto" style={{ maxWidth: `300px`, marginBottom: `1.45rem` }}>
                              <MaxPronkoImage />
                          </div>
                          <h3>Max Pronko</h3>
                          <div className="about-teacher">Max is a developer and programming instructor passionate about making it easier to learn complex skills. He is a former Magento developer who has been involved into evolution of Magento platform before joining Mage Mastery in order to help developers progress along their professional paths.</div>
                      </div>
                      <div className="col col-lg-4" />
                  </div>
              </div>
            <div className="jumbotron jumbotron-fluid mb-0">
              <div className="container">
                <div className="text-center">
                  <h3 className="text-center">Subscribe to our mailing list</h3>
                  <p className="lead">Sign up and don't miss our awesome Mage Mastery video tutorials</p>

                  <div className="mt-4">
                    { this.state.showMessage && <div className={this.state.messageClassName} dangerouslySetInnerHTML={{ __html: this.state.message}} />}
                    <div className="mx-auto form-container">
                      <form onSubmit={this._handleSubmit}>
                        <div className="input-group mb-3 subscribe-input-group">
                          <input type="email"
                                 name="email"
                                 onChange={this._handleChange}
                                 className="form-control"
                                 aria-describedby="button-addon2"
                                 placeholder="Your email here..."
                                 value={this.state.email}
                          />
                          <div className="input-group-append">
                            <button className="btn btn-outline-secondary" type="button" id="button-addon2" onClick={this._handleSubmit}>Subscribe</button>
                          </div>
                        </div>
                      </form>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
      </Layout>
    )
  }
}
